<template lang="html">
  <div class="basemap-list col-12">
    <div
      v-for="b in basemaps"
      :key="b.id"
      cols="12"
      sm="4"
      class="col-4 basemap-container"
      :style="setStyle(b.id, b.thumb)"
       @click="setBasemap(b.id)"
       @mouseover="showByIndex = b.id"
       @mouseout="showByIndex = null"
    >
      <div class="basemap-title font-weight-bold" v-show="showByIndex === b.id">
        {{b.name}}
      </div>
    </div>
  </div>
</template>

<script>
const devEnv = process.env.NODE_ENV === 'development'
const imgURL = devEnv ? '/images/' : `./images/`
import {basemaps} from '@/constants/basemap'
export default {
  name: 'MapManager',
  data() {
    return {
      showByIndex: null,
      basemaps: basemaps,
    };
  },
  computed:{
    selectedBaseMap(){
      return this.$store.state.basemap
    }
  },
  methods:{
    setStyle(id, image){
      return {
        border: id == this.selectedBaseMap ? '5px solid #E6640A' : '1px solid black',
        backgroundImage: "url("+imgURL+image+")"
      }
    },
    setBasemap(id){
      this.$store.commit('changeBaseMap', id)
    }
  },

}
</script>

<style lang="css">
.basemap-list{
  height:100%;
  width:100%;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
}
.basemap-container{
  padding: 0px;
  background-size: cover;
  background-position: center;
  transition: all 0.20s ease;
}
.basemap-container:hover{
  transform: scale(1.4);
}
.basemap-title{
  font-size: 0.8em;
  padding: 5px;
  width: 100%;
  min-height:20%;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  background-color: #E6640A;
  color: white;
}
</style>
