<template lang="html">
  <div class="">
    <h1>Manage icons</h1>
    <v-row justify-center v-show="iconloaded">
      <v-col  cols="12" class="text-center mt-2">
        <h3 class="teal--text lighten-1">You need to upload icons first</h3>
      </v-col>
    </v-row>
    <v-row justify-center>
      <v-col  cols="12" class="text-center mt-2">
        <v-btn color="#E6640A" dark :disabled="iconloaded" @click="reload" large>Reload icons</v-btn>
      </v-col>
      <v-col cols="10" offset-md="1" class="text-center pt-0 mt-7" v-show="!iconloaded">
         <v-text-field color="#f0821E" v-model="number" type="number" label="Number of icons"></v-text-field>
      </v-col>
    </v-row>
    <v-col cols="10" offset-md="1" class="manage-form" v-show="!iconloaded">
        <v-subheader class="pl-0">Icons size in pixels</v-subheader>
        <v-slider
          v-model="size"
          thumb-label="always"
          track-color="#ffc48f"
          thumb-color="#E6640A"
          :disabled="iconloaded"
          color="#f0821E"
        ></v-slider>
      </v-col>
    <v-row justify-center class="mt-0 manage-form" v-show="!iconloaded">
      <v-col md="10" offset-md="1" class="text-center manage-form">
        <v-checkbox v-model="changeColor" label="Change icons colors (only work with SVG icons)" color="#f0821E" class="pt-0 pb-0 mt-0 mb-0 manage-form"></v-checkbox>
      </v-col>
    </v-row>
    <v-row justify-center v-show="changeColor" class="mt-0 manage-form">
      <v-col md="10" offset-md="1" class="text-center mt-0">
        <v-btn color="#f0821E" dark @click="setNewColor">Change</v-btn>
      </v-col>
      <v-col md="10" offset-md="2" class="text-center manage-form">
        <v-color-picker v-model="color" hide-inputs></v-color-picker>
      </v-col>
    </v-row>
    <v-row justify-center v-show="changeColor">
      <v-col md="4" offset-md="4" class="text-center mt-4 manage-form">
        <v-sheet
          color="#648282"
          class="pa-2"
          dark
        >
          <pre>{{ selectedColor }}</pre>
        </v-sheet>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { bus } from '../main'
export default {
  name: 'IconManager',
  data: ()  => ({
    // changeColor: false,
    selectedColor: '#000000'
  }),
  computed:{
    changeColor:{
      get: function() {return this.$store.state.changeColor},
      set: function(value) {this.$store.commit('changeColor', value)}
    },
    iconloaded(){
      return this.$store.state.img64.length == 0
    },
    color:{
      get: function() {return this.$store.state.iconColor},
      set: function(value) {this.selectedColor = value}
    },
    size:{
      get: function() {return this.$store.state.iconSize},
      set: function(value) {this.$store.commit('changeSize', value)}
    },
    number:{
      get: function() {return this.$store.state.iconNumber},
      set: function(value) {this.$store.commit('changeNumber', value)}
    }
  },
  methods:{
    setNewColor(){
      this.$store.commit('changeIconColor', this.selectedColor)
    },
    reload(){
      bus.$emit('reloadIcons')
    }
  }
}
</script>

<style lang="css">
.manage-form{
  padding-top: 0px;
  padding-bottom: 0px;
}
</style>
