<template lang="html">
  <div class="">
    <h1>Upload icons</h1>
    <v-row justify-center>
      <v-col md="10" offset-md="1" v-cloak @drop.prevent="addFile" @dragover.prevent class="d-flex align-center text-center mt-4 drag-zone">
        <h2 v-if="files.length == 0">Drag and drop your icons</h2>
          <v-simple-table v-if="files.length > 0"
            :dense="dense"
            :fixed-header="fixedHeader"
             height="400px"
             class="iconTable"
          >
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">Icon</th>
                  <th class="text-left">Name</th>
                  <th class="text-left">Remove</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="file in files" :key="file.name">
                  <td><img :src="setImageSrc(file.name)" class="iconList"></td>
                  <td>{{ file.name }}</td>
                  <td><v-icon @click="removeFile(file)">mdi-close-circle</v-icon></td>
                </tr>
              </tbody>
            </template>
           </v-simple-table>
      </v-col>
    </v-row>
      <v-btn large :disabled="uploadDisabled" @click="deleteFiles" class="mt-3 mr-2">Delete icons list</v-btn>
      <v-btn large :disabled="uploadDisabled" @click="upload" class="mt-3 ml-2">Load</v-btn>
  </div>
</template>

<script>
  import { img64FromFile } from '@/helpers/fileAPI'
  export default {
    name: 'Uploader',
    data: ()  => ({
      files: [],
      list:[],
      dense: true,
      fixedHeader: true
    }),
    computed:{
      uploadDisabled() {
        return this.files.length === 0;
      }
    },
    methods:{
      addFile(e) {
        let droppedFiles = e.dataTransfer.files;
        if(!droppedFiles) return;
        ([...droppedFiles]).forEach(async(f) => {
          this.files.push(f);
          // List is used to preview icon in table list
          this.list.push({
            name: f.name,
            src: await img64FromFile(f)
          })
        });
      },
      removeFile(file){
        this.files = this.files.filter(f => {
          return f != file;
        });
        this.list = this.list.filter(f => {
          return f.name != file.name;
        })
      },
      deleteFiles(){
        this.files = []
        this.list = []
        this.$store.commit('deleteFiles')
      },
      upload(){
        this.$store.commit('updateFiles', this.files)
        this.$store.commit('updateImg', this.list)
        this.$store.commit('showSideBarMenu', 2)
      },
      setImageSrc(name){
        for (let l of this.list) {
          if (l.name == name) {
            return l.src
          }
        }
      }
    }
  }
</script>

<style lang="css">
  .drag-zone{
    padding-top: 20px;
    border: 2px solid #41B4B4;
    min-height: 200px;
    justify-content: center;
    max-height: 600px;
  }
  .iconList{
    height:30px;
    width:30px;
  }
  .iconTable{
    max-width: 100%;
  }
</style>
