<template lang="html">
  <div id="map">
  </div>
</template>

<script>
import { bus } from '../main'
import { replaceColor } from '@/helpers/imageEncode'
import {basemaps} from '@/constants/basemap'
import L from 'leaflet'

export default {
  name: 'TheMap',
  mounted() {
    this.initMap();
  },
  created(){
    bus.$on('reloadIcons', () => {
      this.loadIcons()
    })
  },
  data: ()  => ({
    dataset: null,
    iconsLayer: null,
    mapBounds: null
  }),
  computed:{
    activeSideBarMenu(){
      return this.$store.state.activeSideBarMenu
    },
    icons(){
      return this.$store.state.img64
    },
    size(){
      return this.$store.state.iconSize
    },
    color(){
      return this.$store.state.iconColor
    },
    changeColor(){
      return this.$store.state.changeColor
    },
    number(){
      return this.$store.state.iconNumber
    },
    basemap(){
      return this.$store.state.basemap
    }
  },
  watch:{
    icons(){
      this.loadIcons()
    },
    size(){
      this.refreshMap()
    },
    color(){
      this.refreshMap()
    },
    number(){
      this.loadIcons()
    },
    basemap(){
      this.makeBasemap()
    }
  },
  methods:{
    initMap(){
      this.map = L.map('map').setView([0, 0], 3)
      this.makeBasemap(this.basemap)
      this.updateBounds()
      this.tileLayer.addTo(this.map)
      this.map.on("moveend", () => {
        this.updateBounds()
      });
    },
    makeBasemap(){
      if (this.map.hasLayer(this.tileLayer)) {
        this.map.removeLayer(this.tileLayer)
      }
      let bm;
      for (let b of basemaps) {
        if (b.id == this.basemap) {
          bm = b
        }
      }
      this.tileLayer = L.tileLayer(bm.url, bm)
      this.tileLayer.addTo(this.map)
    },
    loadIcons(){
      if (this.map.hasLayer(this.iconLayer)) {
        this.map.removeLayer(this.iconLayer)
      }
      this.dataset = []
      for (let icon of this.icons) {
        for (var i = 0; i < this.number; i++) {
          const temp = {
            url: icon.src,
            name: icon.name,
            x: Math.random() * (this.mapBounds._northEast.lng - this.mapBounds._southWest.lng) + (this.mapBounds._southWest.lng),
            y: Math.random() * (this.mapBounds._northEast.lat - this.mapBounds._southWest.lat) + (this.mapBounds._southWest.lat)
          }
          this.dataset.push(temp)
        }
      }
      this.iconLayer = L.layerGroup()
      for (let d of this.dataset) {
        if (this.changeColor) {
          d.url = replaceColor(d.url, this.color)
        }
        const icon = L.icon({
            iconUrl: d.url,
            iconSize: [this.size, this.size*1.5]
        });
        L.marker([d.y, d.x], {icon: icon}).addTo(this.iconLayer).bindPopup(d.name);
      }
      this.iconLayer.addTo(this.map)
    },
    refreshMap(){
      if (this.map.hasLayer(this.iconLayer)) {
        this.map.removeLayer(this.iconLayer)
      }
      this.iconLayer = L.layerGroup()
      for (let d of this.dataset) {
          if (this.changeColor) {
            d.url = replaceColor(d.url, this.color)
          }
          const icon = L.icon({
              iconUrl: d.url,
              iconSize: [this.size, this.size*1.5]
          });
          L.marker([d.y, d.x], {icon: icon}).addTo(this.iconLayer).bindPopup(d.name);
      }
      this.iconLayer.addTo(this.map)
    },
    updateBounds(){
      this.mapBounds = this.map.getBounds()
    }
  }
}
</script>

<style lang="css">
#map{
  height: 100%;
  width:calc(100% - 44px);
}
</style>
