<template lang="html">
  <div class="">
    <div class="sb-container"
      v-for='item in items'
      :key="item.id"
      >
      <v-tooltip
        right
        z-index='1201'
        >
        <template v-slot:activator="{ on }">
          <v-btn icon large v-on="on" @click="setSideBarMenu(item.id)">
            <v-hover v-slot:default="{ hover }">
              <v-icon :color="hover || (actualSideBarMenu == item.id && activeSideBarMenu) ? '#E6640A ': '#ed975e'">{{item.icon}}</v-icon>
            </v-hover>  
          </v-btn>
        </template>
        <span>{{item.title}}</span>
      </v-tooltip>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Sidebar',
  data: () => ({
    //
  }),
  computed:{
    items(){
      return this.$store.state.sidebarItems
    },
    activeSideBarMenu(){
      return this.$store.state.activeSideBarMenu
    },
    actualSideBarMenu(){
      return this.$store.state.actualSideBarMenu
    }
  },
  methods:{
    setSideBarMenu(id){
      if (this.actualSideBarMenu == id && this.activeSideBarMenu) {
        this.$store.commit('hideSideBarMenu')
      } else {
        this.$store.commit('showSideBarMenu', id)
      }
    }
  }
}
</script>

<style lang="css">
  .sb-container{
    padding: 0px;
  }
  .sb-button{
    min-width: 100% !important;
  }
</style>
