import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

// const devEnv = process.env.NODE_ENV === 'development'
export default new Vuex.Store({
  state: {
    activeSideBarMenu: false,
    actualSideBarMenu: 0,
    sidebarItems: [
      {
        id: 1,
        title: "Upload icons",
        icon: "mdi-upload",
        value: 'upload-menu'
      },
      {
        id: 2,
        title: "Manage icons",
        icon: "mdi-settings",
        value: 'upload-menu'
      },
      {
        id: 3,
        title: "Manage map",
        icon: "mdi-map",
        value: 'upload-menu'
      }
    ],
    files: [],
    img64: [],
    changeColor: false,
    iconColor: '#000000',
    iconSize: 15,
    iconNumber: 1,
    basemap:1
  },
  mutations: {
    showSideBarMenu(state, id){
      state.activeSideBarMenu = true
      state.actualSideBarMenu = id
    },
    hideSideBarMenu(state){
      state.activeSideBarMenu = false
    },
    updateFiles(state, files){
      state.files = files
    },
    deleteFiles(state){
      state.files = []
      state.img64 = []
    },
    updateImg(state, list){
      state.img64 = list
    },
    changeColor(state, value){
      state.changeColor = value
    },
    changeIconColor(state, value){
      state.iconColor = value
    },
    changeSize(state, value){
      state.iconSize = value
    },
    changeNumber(state, value){
      state.iconNumber = value
    },
    changeBaseMap(state, value){
      state.basemap = value
    }
  },
  actions: {
  }

});
